@import url(https://fonts.googleapis.com/css?family=Open+Sans|Quicksand);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body{margin:0;height:100%;background-color:#1f2020}.app-container{height:100%;width:100%;display:flex;flex-direction:column}.app-container .app{margin:0 auto;max-width:800px}.app-container .app-header{margin-bottom:32px}.app-container .app-header_top-bar{background-color:#ff2a2b;height:25px}.app-container .app-header_bottom-bar{background-color:#000000;width:100%;height:80px;display:flex;flex-direction:row;justify-content:space-around;align-items:center}.app-container .app-header_bottom-bar h1{display:inline-block;margin-left:1.5rem;margin-right:1.5rem;color:#fff;font-size:1.5em;line-height:1.1;font-family:Quicksand,sans-serif;font-weight:400;font-stretch:condensed;letter-spacing:.1em;text-transform:uppercase;text-shadow:1px 1px 10px rgba(255,255,255,0.25),-1px -1px 10px rgba(255,255,255,0.25);transition:text-shadow 0.8s cubic-bezier(0.5, 0.18, 0.115, 0.87)}.app-container footer{margin-top:16px;display:flex;flex-direction:column;justify-content:center;align-items:center}.app-container footer p{color:white}.app-container footer a{color:#ff2a2b}@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@-webkit-keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@-webkit-keyframes expandingLine{from,10%{stroke-dashoffset:0}50%,to{stroke-dashoffset:-112px}from,10%,to{stroke-dasharray:94px 19px}50%,60%{stroke-dasharray:.4px 112px}}@keyframes expandingLine{from,10%{stroke-dashoffset:0}50%,to{stroke-dashoffset:-112px}from,10%,to{stroke-dasharray:94px 19px}50%,60%{stroke-dasharray:.4px 112px}}@-webkit-keyframes colorMorphing{0%,35%,100%{stroke:#31313D}50%,85%{stroke:#919194}}@keyframes colorMorphing{0%,35%,100%{stroke:#31313D}50%,85%{stroke:#919194}}.loading-spinner{font-size:40px;height:40px;margin:16px 0;position:relative;width:40px}.loading-spinner-container{align-items:center;display:flex;flex-direction:column;justify-content:center;width:100%;position:absolute;top:40%}.loading-spinner-front{fill:transparent;font-size:40px;height:40px;overflow:visible;position:absolute;stroke-linecap:round;stroke-width:4px;top:0;width:40px;-webkit-animation:2s rotate infinite linear;animation:2s rotate infinite linear}.loading-spinner-front circle{stroke-dasharray:94px 19px;-webkit-animation:2s expandingLine infinite ease-in-out, 4s colorMorphing infinite linear;animation:2s expandingLine infinite ease-in-out, 4s colorMorphing infinite linear}.loading-spinner-rear{fill:transparent;font-size:40px;height:40px;overflow:visible;position:absolute;stroke-linecap:round;stroke-width:4px;top:0;width:40px}.loading-spinner-rear circle{stroke-opacity:.15;-webkit-animation:15s colorMorphing infinite linear;animation:15s colorMorphing infinite linear}

.landing-container{display:grid;grid-template-columns:repeat(2, 1fr);justify-items:center;grid-gap:32px}.link-button{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;background:#1f2020;border:1px solid;border-radius:0;line-height:1.1;color:#ffffff;font-family:Quicksand,sans-serif;font-stretch:condensed;font-weight:400;letter-spacing:1px;text-shadow:1px 1px 10px rgba(255,255,255,0.25),-1px -1px 10px rgba(255,255,255,0.25);text-transform:uppercase;text-decoration:none;cursor:pointer}.link-button:hover{background-color:#2c2d2d}.link-button:active{border-color:#f2f2f2;background-color:#121313}.link-button:focus{outline:1px dotted #121313;outline-offset:2px}

.killer-container button{width:150px;height:48px;background-color:#ff2a2b;color:#fff;font-family:Quicksand,sans-serif;margin-bottom:16px;border:2px solid;border-color:#ff2a2b;background-color:#ff2a2b;font-size:14px;padding:8px;border-radius:3px;cursor:pointer;font-weight:700;outline-offset:3px;transition:border-color 0.2s;transition:background-color 0.2s}.killer-container button:hover{background-color:#f44;border-color:#f44}.killer-container button:active{border-color:#ff1112;background-color:#ff1112}.killer-container button:focus{outline:1px dotted #ff1112;outline-offset:2px}.killer-container h2,.killer-container p{color:white;text-shadow:1px 1px 10px rgba(255,255,255,0.25),-1px -1px 10px rgba(255,255,255,0.25)}.killer-container .killer{display:grid;grid-gap:16px;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(2, 1fr)}.killer-container .killer img{max-width:100%;max-height:140px}.killer-container .killer>*{display:flex;flex-direction:column;align-items:center}.killer-container .killer_name{grid-column:span 2}.killer-container .killer_addons,.killer-container .killer_perks{grid-column:span 1}

.perk-icon{transform:rotateY(0deg) rotate(45.5deg);width:50%;margin-top:20px;border:10px solid black}.perk-icon img{transform:rotateY(0deg) rotate(-45.5deg)}

.addon-icon{height:100%;max-width:100%;border:10px solid black}

.portrait-icon{background-color:#61513c;border:10px solid black}

.survivor-container button{width:150px;height:48px;background-color:#ff2a2b;color:#fff;font-family:Quicksand,sans-serif;margin-bottom:16px;border:2px solid;border-color:#ff2a2b;background-color:#ff2a2b;font-size:14px;padding:8px;border-radius:3px;cursor:pointer;font-weight:700;outline-offset:3px;transition:border-color 0.2s;transition:background-color 0.2s}.survivor-container button:hover{background-color:#f44;border-color:#f44}.survivor-container button:active{border-color:#ff1112;background-color:#ff1112}.survivor-container button:focus{outline:1px dotted #ff1112;outline-offset:2px}.survivor-container h2,.survivor-container p{color:white;text-shadow:1px 1px 10px rgba(255,255,255,0.25),-1px -1px 10px rgba(255,255,255,0.25)}.survivor-container .survivor{display:grid;grid-gap:16px;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(2, 1fr)}.survivor-container .survivor img{max-width:100%;max-height:140px}.survivor-container .survivor>*{display:flex;flex-direction:column;align-items:center}.survivor-container .survivor_name,.survivor-container .survivor_item,.survivor-container .survivor_addons,.survivor-container .survivor_perks{grid-column:span 1}.survivor-container .survivor_perks{grid-row-start:2}

